@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
*{font-family:"Open Sans",sans-serif}.profile-image{border-radius:50%;border:.5rem solid #27ae60;margin-left:auto;width:100%;max-width:350px;height:auto;box-shadow:rgba(0,0,0,.1) 0 30px 30px -30px;transition:all .3s ease-out}.profile-image:hover{box-shadow:none}.prof-title{margin:0;font-size:40px;font-weight:400}.location-div{text-shadow:#ffebcd}.opp-div{padding-bottom:1rem}.row{display:flex}.main-content-profile{flex:80% 1}.image-content-profile{text-align:right;flex:20% 1;max-width:100%;height:auto}.bio-text{line-height:30px;font-size:22px}.desc-prof{line-height:45px;font-size:19px}.subTitle{color:#868e96}@media(max-width: 768px){.row{display:flex;flex-direction:column}.main-content-profile{text-align:center;order:2}.bio-text{font-size:16px}.image-content-profile{margin-left:auto;margin-right:auto;order:1}.desc-prof{line-height:30px;font-size:16px}.profile-image{margin-left:auto;margin-right:auto;width:100%;max-width:250px;height:auto}.profile-image:hover{box-shadow:rgba(0,0,0,.1) 0 50px 50px -50px;transition:.3s ease-out}.prof-title{padding-bottom:1rem;font-size:32px;text-align:center}}
